import { gql } from '@apollo/client/core';
import client from '../apollo-client'; // Adjust the path as necessary
import cloneDeep from 'lodash/cloneDeep'; // Ensure lodash is properly imported
import html2pdf from 'html2pdf.js';
import Papa from 'papaparse';
import CountdownTimer from './CountdownTimer';
import axios from 'axios';

// import Papa from 'papaparse';


const UPDATE_CALLME = gql`
  # Write your query or mutation here
mutation(
  $id: ID!
  $no: Int
  $user: String
  $name: String
  $phoneNumber: String
  $status: String
  $interaction: String
  $lastCall: DateTime
  $nextCall: DateTime
) {
  updateMakeCall(
    id: $id
    data: {
      user: $user
      name: $name
      phoneNumber: $phoneNumber
      status: $status
      interaction: $interaction
      lastCall: $lastCall
      nextCall: $nextCall
      no: $no
    }
  ) {
    # Define the fields you want to be returned here

    data{
      id
      attributes{
    name
    phoneNumber
    status
    interaction
    lastCall
    nextCall
    no
    }
    }
  }
}

`;

// const CREATE_CALLME = gql`
// mutation (
//   $name: String
//   $status:String
//   $no: Int
//   $user: String
//   $phoneNumber: String
//   $interaction: String
//   $lastCall: DateTime
//   $nextCall: DateTime
//  ) {
//   createMakeCall(
//     data: {
//       name: $name
//       status: $status
//      no: $no
//   user: $user
//   phoneNumber: $phoneNumber
//   interaction: $interaction
//   lastCall: $lastCall
//   nextCall: $nextCall
//      }
//   ) {
//     data {
//       id
//     }
//   }
// }`

// const QUERY_CALLME = gql`
//   query($user: String!) {
//   makeCalls(filters: { user: { eq: $user } }, pagination: { limit: 1000 }) {
//     data {
//       id
//       }
//     }
