// `




export default {
  name: 'ContactList',
  props: {
    projectKey: {
      type: String,
      required: true
    },
    enDate: {
      type: String,
      required: true
    }
  },
  components: {
    vSelect: window["vue-select"],
    CountdownTimer
  },
  setup() {
    const contacts = ref([]);
    const currentIndex = ref(0); // Initialize currentIndex
    const dataRestored = ref(false); // Flag to check if data has been restored
    const selectedUser = ref(null);

    // const datas = ref([{id: 1, status: 1, items:[1,2,3]}, {id:2,  status: 2, items:[1,2,3]}, {id:3,  status: 3, items:[1,2,3]}]);

    onMounted(() => {
      const savedIndex = localStorage.getItem('currentIndex');
      if (savedIndex !== null) {
        currentIndex.value = parseInt(savedIndex, 10);
      }

      selectedUser.value = localStorage.getItem('user');
    });

    // Watch currentIndex and save it to localStorage whenever it changes
    watch(currentIndex, (newIndex) => {
      if (newIndex === null) {
        const savedIndex = localStorage.getItem('currentIndex');
        if (savedIndex !== null) {
          currentIndex.value = parseInt(savedIndex, 10);
        }
      }
      else {
        localStorage.setItem('currentIndex', newIndex);
      }
    });


    watch(selectedUser, (newUser) => {
      localStorage.setItem('user', newUser);
    });

    // const convertYMD2DMY = (dateString) => {
    //   let [year, month, day] = dateString.split('-');
    //   month = month.padStart(2, '0');
    //   day = day.padStart(2, '0');
    //   return `${day}-${month}-${year.substring(2)}`;
    // };
    const todayDDMMYYYY = () => {
      const date = new Date();
      const day = String(date.getDate()).padStart(2, '0'); // Add leading zero if needed
      const month = String(date.getMonth() + 1).padStart(2, '0'); // Months are 0-indexed
      const year = date.getFullYear();

      return `${day}/${month}/${year}`;
    };

    const convertDMY2YMD = (dateString) => {
      let [day, month, year] = dateString.replaceAll('/', '-').split('-');
      if (day < 1000) {  // convert only if day is not a year 
        month = month.padStart(2, '0');
        day = day.padStart(2, '0');
        return `${year}-${month}-${day}T00:00:00Z`;
      }
      else {
        return `${day}-${month}-${year}T00:00:00Z`;
      }
    };

    const { mutate: updateCallme } = useMutation(UPDATE_CALLME);


    const makeCall = async (contact) => {
      try {
        await new Promise(resolve => setTimeout(resolve));
        let { name, phoneNumber, status, interaction, lastCall, nextCall, no } = contact;
        lastCall = convertDMY2YMD(lastCall);
        nextCall = convertDMY2YMD(nextCall);


        let user = selectedUser;
        let id = 0;



        if (user.value.toLowerCase() === "alyssa") id = 3;
        if (user.value.toLowerCase() === "alvin") id = 4;
        if (user.value.toLowerCase() === "ashton") id = 2;


        const variables =
          { id: id, user: user.value, name: name, phoneNumber: phoneNumber, status: status, interaction: interaction, lastCall: lastCall, nextCall: nextCall, no: no }
        // {id,user ,name,phoneNumber, status, interaction, lastCall, nextCall,no}
        // {id:id,data:{user ,name,phoneNumber, status, interaction, lastCall, nextCall,no}}




        await updateCallme(variables);

        // console.log('Updated callme:', data.updateCallme.data);

        contact.called = (function (calledString) {
          // Attempt to convert the string to a number
          const numberValue = parseInt(calledString, 10);

          // Check if conversion was successful (is a number)
          if (!isNaN(numberValue)) {
            return numberValue + 1; // Increment the number
          } else {
            return 1; // Set to zero if not numeric
          }
        })(contact.called);

        contact.date = todayDDMMYYYY();
        // Perform any additional logic with the updated data

      } catch (error) {
        console.error('Error updating callme:', error);
        // Handle error appropriately
      }
      currentIndex.value = contacts.value.findIndex(c => c.no === contact.no);
    }

    const saveDataToLocalStorage = () => {
      console.log('Attempting to save data to local storage...');
      if (dataRestored.value) {
        try {
          const dataToSave = {
            contacts: contacts.value,
            // Add other properties as needed
          };
          localStorage.setItem('appData', JSON.stringify(dataToSave));
          console.log('Data saved to local storage:', dataToSave);
        } catch (error) {
          console.error('Error saving to local storage:', error);
        }
      } else {
        console.log('Data not saved: dataRestored.value is false');
      }
    };


    const restoreDataFromLocalStorage = () => {
      console.log('Attempting to restore data from local storage...');
      const savedData = localStorage.getItem('appData');
      if (savedData) {
        try {
          console.log('Saved data found in local storage:', savedData);
          const parsedData = JSON.parse(savedData);
          contacts.value = parsedData.contacts || [];
          // Restore other properties as needed
          dataRestored.value = true;
          console.log('Data restored from local storage:', parsedData);
        } catch (error) {
          console.error('Error parsing data from local storage:', error);
        }
      } else {
        console.log('No saved data found in local storage.');
      }
    };

    onMounted(() => {
      restoreDataFromLocalStorage();

      // Additional initialization logic if needed
    });

    // Watch contacts for changes and save to localStorage
    watchEffect(() => {
      console.log('watchEffect triggered, contacts:', contacts.value);
      //  saveDataToLocalStorage();
    });
    let initialContacts = cloneDeep(contacts.value);
    // Watch for changes in contacts
    watch(contacts, (newContacts) => {
      console.log('Contacts changed');

      // Check if there are any changes compared to the initial state
      let isDirty = false;

      // Ensure initialContacts and newContacts are arrays of the same length
      if (newContacts.length !== initialContacts.length) {
        isDirty = true;
      } else {
        for (let i = 0; i < newContacts.length; i++) {
          const newContact = newContacts[i];
          const initialContact = initialContacts[i];

          // Check if newContact or initialContact is undefined
          if (!newContact || !initialContact) {
            isDirty = true;
            break;
          }

          // Compare each field of newContact with initialContact
          for (const key in newContact) {
            if (newContact[key] !== initialContact[key]) {
              console.log(`Field '${key}' in contact ${newContact.id} changed`);
              isDirty = true;
              break; // No need to check further fields if one is different
            }
          }

          if (isDirty) break; // No need to check further contacts if one is different
        }
      }

      // If any data is dirty, save to localStorage and update the initialContacts
      if (isDirty) {
        dataRestored.value = true;
        saveDataToLocalStorage();
        initialContacts = cloneDeep(newContacts); // Update the initial state to the current state
      }
    }, { deep: true });

    return {
      restoreDataFromLocalStorage,
      makeCall,
      contacts,
      currentIndex,
      selectedUser
    };
  },
  mounted() {
    this.loadMessageFiles();
  },
  data() {
    return {
      showCountdown: false, // Initially hide the CountdownTimer
      filterTerm: '',
      sortOrder: {
        phoneNumber: 'asc',
        name: 'asc',
        no: 'asc',
        status: 'asc',
        project: 'asc',
        nextCall: 'asc'
      },
      users: ["Alyssa", "Ashton", "Alvin"],

      countries: [
        { country: "Spain" },
        { country: "France" },
        { country: "Ghana" },
        { country: "Nigeria" },
        { country: "Brazil" },
        { country: "Ecuador" }
      ],


      headers: [
        { text: '', title: '', value: 'remove', width: '2%' },
        { text: '', title: '', value: 'send', width: '2%' },
        { text: '', title: '', value: 'call', width: '2%' },
        { text: '', title: '', value: 'dial', width: '2%' },
        { text: 'Project', title: 'Project', value: 'project', width: '3%' },
        { text: 'Source', title: 'Source', value: 'source', width: '2%' },
        { text: 'No', title: 'No', value: 'no', width: '3%' },
        { text: 'Name', title: 'Name', value: 'name', width: '12%' },
        { text: 'Language', title: 'Lgn.', value: 'language', width: '1%' },
        { text: 'Phone', title: 'Phone', value: 'phoneNumber', width: '12%' },
        { text: 'Interaction', title: 'Interaction', value: 'interaction', width: '26%' },
        { text: 'Status', title: 'Status', value: 'status', width: '10%' },
        { text: 'Doc', title: 'Doc', value: 'doc', width: '5%' },
        { text: 'Called', title: 'Called', value: 'called', width: '4%' },
        { text: 'Contacted', title: 'Contacted', value: 'contacted', width: '3%' },
        { text: 'Dial Date', title: 'Dial', value: 'date', width: '5%' },
        { text: 'Last Called Date', title: 'Last Call', value: 'lastCall', width: '7%' },
        { text: 'Next Call', title: 'Next_Call', value: 'nextCall', width: '7%' },
        { text: 'Next Action', title: 'Next Action', value: 'na1', width: '5%' },
      ],
      availableDates: this.generateDateOptions(),
      messageEn: [],
      messageCn: [],
      messageBs: [],
      phoneNumber: '',
      sendmsg: "",
      whatsappmessage: '',
      findValue: null,
      //  currentIndex: 0,
      error: null,
      // contacts: [
      //   { no: 1, project: 'abc', source: 'uom', name: 'John', phoneNumber: '+601116331800', interaction: 'hello', message: '!ei2', status: '', na1: "w", date: "10-10-2024", email: "aa@bb.com", occupation: "IT", lastCall: "10-10-2024", nextCall: "10-10-2024" },
      //   { no: 2, project: 'abc', source: 'uom', name: 'Jane', phoneNumber: '+601116331801', interaction: '', message: '!ei2', status: '', na1: "w", date: "10-10-2024", email: "aa@bb.com", occupation: "IT", lastCall: "10-10-2024", nextCall: "10-10-2024" },
      //   { no: 3, project: 'abc', source: 'uom', name: 'Alex', phoneNumber: '+601116331802', interaction: '', message: '!ei2', status: '', na1: "w", date: "10-10-2024", email: "aa@bb.com", occupation: "IT", lastCall: "10-10-2024", nextCall: "10-10-2024" }
      //   // Add more contacts as needed
      // ],
    };
  },
  computed: {
    currentContact() {
      return this.contacts[this.currentIndex] || {};
    },
    //   filteredContacts() {
    //   if (this.filterTerm === '') {
    //     return this.contacts; // Show all records
    //   } else if (this.filterTerm.toLowerCase() === 'dup') {
    //     return this.getDuplicateRecords(); // Show only duplicate records
    //   } else {
    //     return this.contacts.filter(contact => {
    //       return Object.values(contact).some(value =>
    //         value.toString().toLowerCase().includes(this.filterTerm.toLowerCase())
    //       );
    //     });
    //   }
    // }

    filteredContacts() {
      if (this.filterTerm === '') {
        return this.contacts; // Show all records
      } else if (this.filterTerm.toLowerCase() === 'dup') {
        return this.getDuplicateRecords(); // Show only duplicate records
      } else {
        return this.contacts.filter(contact => {
          // Define the specific fields you want to search through
          const fieldsToSearch = [contact.name, contact.phoneNumber, contact.status]; // Add more fields if needed
          return fieldsToSearch.some(value =>
            value.toString().toLowerCase().includes(this.filterTerm.toLowerCase())
          );
        });
      }
    }
  },



  methods: {
    async loadContactsFromStrapi(projectFilter) {
      // Prompt the user for how to handle existing contacts
      const action = confirm('Do you want to overwrite existing contacts or append the new ones? Click "OK" to overwrite or "Cancel" to append.');

      // If the user selects "OK", clear existing contacts
      if (action) {
        this.contacts = [];
      }

      const fetchContactsQuery = gql`
    query($project: String!) {
      leads(filters: { project: { eq: $project } }) {
        data {
          id
          attributes {
            project
            user
            name
            phoneNumber
            status
            interaction
            lastCall
            nextCall
            called
            contacted
            language
            message
            doc
            na1
            source
            occupation
          }
        }
      }
    }
  `;

      try {
        const { data } = await client.query({
          query: fetchContactsQuery,
          variables: { project: projectFilter },
          fetchPolicy: 'no-cache',
        });

        // Iterate over each contact fetched from Strapi
        data.leads.data.forEach((lead) => {
          const existingContact = this.contacts.find(contact => contact.phoneNumber === lead.attributes.phoneNumber);

          const newContact = {
            no: this.contacts.length + 1,
            user: lead.attributes.user || '',
            name: lead.attributes.name || '',
            phoneNumber: lead.attributes.phoneNumber || '',
            project: lead.attributes.project || '',
            occupation: lead.attributes.occupation || '',
            status: lead.attributes.status || '',
            interaction: lead.attributes.interaction || '',
            lastCall: this.convertDMY2YMD(lead.attributes.lastCall) || '1-1-1900',
            nextCall: this.convertDMY2YMD(lead.attributes.nextCall) || '1-1-1900',
            called: lead.attributes.called || 0,
            contacted: lead.attributes.contacted || 0,
            language: lead.attributes.language || '',
            message: lead.attributes.message || '',
            doc: lead.attributes.doc || '',
            na1: lead.attributes.na1 || '',
            source: lead.attributes.source || '',
            edit: false,
            editField: ''
          };

          if (existingContact && !action) {
            // If appending and contact exists, update it
            Object.assign(existingContact, newContact);
          } else {
            // Add new contact
            this.contacts.push(newContact);
          }
        });

        // Sort contacts by phoneNumber and remove duplicates
        this.contacts.sort((a, b) => (a.phoneNumber < b.phoneNumber ? -1 : 1));
        this.removeDuplicatePhoneNumbers();
      } catch (error) {
        console.error('Error loading contacts from Strapi:', error);
      }
    },


    convertDMY2YMD(dateString) {
      // Check if dateString is valid
      if (!dateString) return null;

      // Replace different delimiters with '/'
      const formattedDateString = dateString.replace(/-/g, '/');

      // Split the date string into components
      const [day, month, year] = formattedDateString.split('/').map(Number);

      // Validate date components
      if (isNaN(day) || isNaN(month) || isNaN(year)) {
        console.error('Invalid date components');
        return null;
      }

      // Create a new Date object with the components
      const date = new Date(year, month - 1, day); // Month is zero-based in JavaScript

      // Check if the date was created correctly
      if (date.getFullYear() !== year || date.getMonth() !== month - 1 || date.getDate() !== day) {
        console.error('Invalid date');
        return null;
      }

      // Format the date as YYYY-MM-DDTHH:mm:ss
      const yearStr = date.getFullYear();
      const monthStr = String(date.getMonth() + 1).padStart(2, '0'); // Months are zero-based
      const dayStr = String(date.getDate()).padStart(2, '0');
      const hoursStr = '00';
      const minutesStr = '00';
      const secondsStr = '00';

      // Return null for invalid default date
      if (yearStr === '1900') {
        return null;
      }

      return `${yearStr}-${monthStr}-${dayStr}T${hoursStr}:${minutesStr}:${secondsStr}Z`;
    }
    ,
    async exportToStrapi() {
      const createMutation = gql`
        mutation CreateLead(
          $no: Int
          $user: String
          $name: String
          $phoneNumber: String
          $status: String
          $interaction: String
          $lastCall: DateTime
          $nextCall: DateTime
          $called: Int
          $contacted: Int
          $language: String
          $message: String
          $doc: String
          $na1: String
          $source: String
          $project: String
        ) {
          createLead(
            data: {
              user: $user
              name: $name
              phoneNumber: $phoneNumber
              status: $status
              interaction: $interaction
              lastCall: $lastCall
              nextCall: $nextCall
              called: $called
              no: $no
              language: $language
              contacted: $contacted
              message: $message
              doc: $doc
              na1: $na1
              source: $source
              project: $project
            }
          ) {
            data {
              id
              attributes {
                name
                phoneNumber
                status
                interaction
                lastCall
                nextCall
                no
                project
                source
              }
            }
          }
        }
      `;

      const updateMutation = gql`
        mutation UpdateLead(
          $id: ID!
          $no: Int
          $user: String
          $name: String
          $phoneNumber: String
          $status: String
          $interaction: String
          $lastCall: DateTime
          $nextCall: DateTime
          $called: Int
          $contacted: Int
          $language: String
          $message: String
          $doc: String
          $na1: String
          $source: String
          $project: String
        ) {
          updateLead(
            id: $id
            data: {
              user: $user
              name: $name
              phoneNumber: $phoneNumber
              status: $status
              interaction: $interaction
              lastCall: $lastCall
              nextCall: $nextCall
              called: $called
              no: $no
              language: $language
              contacted: $contacted
              message: $message
              doc: $doc
              na1: $na1
              source: $source
              project: $project
            }
          ) {
            data {
              id
              attributes {
                name
                phoneNumber
                status
                interaction
                lastCall
                nextCall
                no
              }
            }
          }
        }
      `;

      try {
        for (const contact of this.contacts) {
          const existingContactQuery = gql`
            query GetLead($phoneNumber: String!) {
              leads(filters: { phoneNumber: { eq: $phoneNumber } }) {
                data {
                  id
                  attributes {
                    source
                    project
                    name
                    phoneNumber
                    status
                    interaction
                    lastCall
                    nextCall
                    called
                    contacted
                    language
                    message
                    doc
                    na1
                  }
                }
              }
            }
          `;

          const { data } = await client.query({
            query: existingContactQuery,
            variables: { phoneNumber: contact.phoneNumber },
            fetchPolicy: 'no-cache',
          });

          //     if (data.leads.data.length > 0) {
          if (Array.isArray(data.leads.data) && data.leads.data.length > 0) {
            const existingContact = data.leads.data[0].attributes;

            if (existingContact.source !== contact.source) {
              alert(`${contact.phoneNumber} exists in both server ${existingContact.source} and local ${contact.source}`);
              return;
            }

            if (existingContact.name !== contact.name) {
              alert(`${contact.name} with phone number ${contact.phoneNumber} exists in both server ${existingContact.name} and local ${contact.name}`);
              return;
            }

            const fieldsToUpdate = {};
            for (const field in contact) {
              if (contact[field] !== existingContact[field] && field !== 'id') {
                fieldsToUpdate[field] = contact[field];
              }
            }

            if (Object.keys(fieldsToUpdate).length > 0) {
              await client.mutate({
                mutation: updateMutation,
                variables: {
                  id: data.leads.data[0].id,
                  ...fieldsToUpdate,
                  contacted: parseInt(contact.contacted, 10),
                  called: parseInt(contact.called, 10),
                  lastCall: this.convertDMY2YMD(contact.lastCall),
                  nextCall: this.convertDMY2YMD(contact.nextCall),
                },
              });
              console.log(`Updated contact: ${contact.name}`);
            }
          } else {

            await client.mutate({
              mutation: createMutation,
              variables: {
                ...contact,
                no: parseInt(contact.no, 10),
                contacted: parseInt(contact.contacted, 10),
                called: parseInt(contact.called, 10),
                lastCall: this.convertDMY2YMD(contact.lastCall),
                nextCall: this.convertDMY2YMD(contact.nextCall),
              },
            });
            console.log(`Created new contact: ${contact.name}`);
          }
        }
      } catch (error) {
        console.error('Error syncing contacts with Strapi:', error);
      }
      alert('Data synchronization completed!');
    },




