    removeContact(index) {
      // Get the contact from the filtered list
      const contactToRemove = this.filteredContacts[index];

      // Confirm with the user before deleting
      const confirmation = confirm(`Are you sure you want to delete the contact: ${contactToRemove.name} (${contactToRemove.phoneNumber})?`);

      // If the user confirms, proceed with the deletion
      if (confirmation) {
        // Find the index of this contact in the original contacts array
        const originalIndex = this.contacts.findIndex(contact => contact === contactToRemove);

        // Debugging: Log the original index and the contact to be removed
        console.log('Removing contact at original index:', originalIndex);
        console.log('Contact:', this.contacts[originalIndex]);

        // Remove the contact from the original contacts array if it exists
        if (originalIndex !== -1) {
          this.contacts.splice(originalIndex, 1);
        }
      } else {
        // If the user cancels, log a message (optional)
        console.log('Contact removal canceled.');
      }
    },
    getDuplicateRecords() {
      const phoneNumberCount = this.contacts.reduce((acc, contact) => {
        const phoneNumber = contact.phoneNumber.trim();
        if (phoneNumber) {
          acc[phoneNumber] = (acc[phoneNumber] || 0) + 1;
        }
        return acc;
      }, {});

      const duplicateNumbers = Object.keys(phoneNumberCount).filter(number => phoneNumberCount[number] > 1);

      return this.contacts.filter(contact => duplicateNumbers.includes(contact.phoneNumber.trim()));
    },
    stopCountdownFromParent() {
      this.$refs.countdown.stopCountdown();
    },
    async sendMessage() {
      this.currentIndex = 0;
      while (this.currentIndex < this.contacts.length) {
        // Check if the current contact has already been sent a message
        if (this.contacts[this.currentIndex].status.trim() !== 'send' && this.contacts[this.currentIndex].phoneNumber.trim().length > 0) {
          const contact = this.contacts[this.currentIndex];
          let language = "e";

          if (contact.language !== undefined && contact.language.trim() !== "") {
            language = contact.language;
          }


          try {
            // if (contact.status.trim().length > 0) {
            //   alert(contact.status + ":" + contact.phoneNumber);

            // }

            let message = "!" + language + this.sendmsg;
            let msg = this.getmessage(message, contact);
            contact.status = "send";
            if (contact.doc !== undefined)
              contact.doc = this.sendmsg + contact.doc;
            else
              contact.doc = this.sendmsg


            //      alert("calling :" + contact.name + ":" +  contact.phoneNumber + ":"+ msg);

            const response = await axios.post('http://localhost:3000/send-message', {
              number: contact.phoneNumber,
              message: msg
            });

            if (response.data.success) {
              // Optionally handle successful message sending
            } else {
              alert('Failed to send message: ' + response.data.error);
              contact.status = "error 1 send";
              this.$refs.countdown.stopCountdown();
              return; // Exit the function if message sending fails
            }
          } catch (error) {
            console.error('Error:', error);
            contact.status = "error 2 send";
            alert('An error occurred: ' + error.message);
            this.$refs.countdown.stopCountdown();
            return; // Exit the function if an error occurs
          }
          this.currentIndex++;
          break;
        }
        // Move to the next contact
        this.currentIndex++;

      }

      if (this.currentIndex >= this.contacts.length) {
        this.$refs.countdown.stopCountdown();
        alert("send whatapp message completed");
      }
    },

