    sortContacts(property) {
      if (this.sortOrder[property] === 'asc') {
        this.contacts.sort((a, b) => {
          if (a[property] < b[property]) return -1;
          if (a[property] > b[property]) return 1;
          return 0;
        });
        this.sortOrder[property] = 'desc';
      } else {
        this.contacts.sort((a, b) => {
          if (a[property] < b[property]) return 1;
          if (a[property] > b[property]) return -1;
          return 0;
        });
        this.sortOrder[property] = 'asc';
      }
    },
    formatNumberWithLeadingZeros(number) {
      // Convert the number to a string
      const numStr = String(number);
      // Pad the string with leading zeros
      const paddedStr = numStr.padStart(6, '0');
      return paddedStr;
    },
    sortContactsAndNumber(property) {
      if (this.sortOrder[property] === 'asc') {
        this.contacts.sort((a, b) => {
          if (a[property] + this.formatNumberWithLeadingZeros(a['no']) < b[property] + this.formatNumberWithLeadingZeros(b['no'])) return -1;
          if (a[property] + this.formatNumberWithLeadingZeros(a['no']) > b[property] + this.formatNumberWithLeadingZeros(b['no'])) return 1;
          return 0;
        });
        this.sortOrder[property] = 'desc';
      } else {
        this.contacts.sort((a, b) => {
          if (a[property] + a['no'] < b[property] + a['no']) return 1;
          if (a[property] + a['no'] > b[property] + a['no']) return -1;
          return 0;
        });
        this.sortOrder[property] = 'asc';
      }
    },

