    generateDateOptions() {
      const dates = [];
      const today = new Date();
      for (let i = 0; i < 30; i++) {
        const date = new Date(today);
        date.setDate(today.getDate() + i);

        const day = String(date.getDate()).padStart(2, '0'); // Get day and pad with leading zero if needed
        const month = String(date.getMonth() + 1).padStart(2, '0'); // Get month (zero-based) and pad with leading zero if needed
        const year = date.getFullYear(); // Get full year

        const formattedDate = `${day}/${month}/${year}`; // Format as DD/MM/YYYY
        dates.push(formattedDate);
      }
      return dates;
    },
    addPlus(np) {
      if (np.substring(0, 2) === "60")
        np = "+" + np;
      return np.replaceAll("@", "");
    },
    formatString(text) {
      if (text === undefined) {
        return '';
      }

      // Remove all non-numeric characters
      text = text.replace(/\D/g, '');

      if (text.length <= 12) {
        return text.padEnd(12, '_'); // Pad with underscores if shorter
      } else {
        return text.slice(0, 12); // Truncate if longer
      }
    },
    loadMessageFiles() {
      this.loadTextFile('/messageEn.txt', 'messageEn');
      this.loadTextFile('/messageCn.txt', 'messageCn');
      //  this.loadTextFile('/messageBs.txt', 'messageBs');

